input{background-color:#f5f5f5;color:#000;padding:8px;border:1px solid #726e6e;border-radius:4px;font-size:14px}.header-container{width:100%;padding:10px 20px;background-color:#4d4d60;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.header-container .Logo{color:#fff;font-size:24px;font-weight:700}.header-container .login-wrapper{display:flex;flex-direction:column;align-items:flex-end}.header-container .login-title{color:#fff;font-size:16px;margin-bottom:10px}.header-container .login-form{display:flex;align-items:center;gap:10px}.header-container .login-form .login-button{background-color:#856825;color:#fff;border:none;padding:8px 25px;border-radius:4px;cursor:pointer;transition:background-color .3s ease}.header-container .login-form .login-button:hover{background-color:#684f1f}.containerSwal{padding:20px;text-align:center;width:100%}.containerSwal .topContainer{display:flex;flex-direction:row;justify-content:flex-start;align-items:flex-start;gap:40px}.containerSwal .topContainer .inputSearch{padding:8px;margin-bottom:10px;width:300px}.containerSwal table{width:100%;border-collapse:collapse;margin:20px 0}.containerSwal table th,.containerSwal table td{border:1px solid #ccc;padding:10px 15px}.containerSwal table tbody tr{cursor:pointer;transition:background-color ease .3s}.containerSwal table tbody tr:hover{background-color:#eeebeb}.containerSwal table input{width:100%;padding:8px}.containerSwal .paginacion{margin:10px 0}.containerSwal .paginacion button{margin:0 10px}.containerSwal .addButton{margin-top:10px;background-color:#4caf50;color:#fff;padding:10px;border:none;cursor:pointer}